@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;600;800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header {
    background: hsl(0, 0%, 100%) !important;
    color: hsl(200, 15%, 8%) !important;
  }

  

  .toggle__mode{
    display: flex;
    align-items: center;
  }
  
  .header__nav {
    display: flex !important;
    align-items: center;
    justify-content: space-between;
  }

  .night__btn{
    text-transform: capitalize !important;
  }
  

  @media screen and (max-width: 768px){
    .header{
      padding: 18px 0;
    }

    .header__nav h3 {
      font-size: 1rem;
    }

    .header__nav {
      padding: 0 !important;
    }
  }
  

  .card__text label {
    color:  hsl(200, 15%, 8%);
    font-weight: 600 !important;
  }
  
  .grid__flex{
    display: flex;
    justify-content: space-evenly;
    height: 100%;
  }
  
  .card__flex{
    display: flex;
    flex-direction: column;
    flex: 1 1;
    transition: all .5s ease !important;
  }

  .card__flex:hover{
    transform: scale(0.9);
  }

  .card__text h4{
    margin-bottom: 10px;
  }

  .link{
    color: #222 !important;
    text-decoration: none;
  }
  

.search__area {
    margin: 50px 0;
    align-items: center;
    display: flex;
    justify-content: space-between; 
  }
  
  .search__box1 {
    position: relative;
    display: flex;
    align-items: center;
    width: 500px;
  }
  
  .search__box1 .search__box{
    font-size: 1rem;
    line-height: 1.7;
    width: 100%;
    padding: 18px 20px 10px 50px;
    outline: none;
    border: 1px solid #ccc;
    border-radius: 5px;
    color: #000;
    font-family: 'Nunito sans', 'san-serif';
    box-shadow: 0px 2px 5px 0px #ccc;
    border: none;
  }

  
  .search__icon{
    position: absolute;
    top: 19px;
    left: 20px;
    color: #aaa;
    font-size: 23px !important;
  }
  
  
  .search__box2 .input__select {
    width: 300px !important;
    outline: none !important;
    background: hsl(0, 0%, 100%) !important;
    box-shadow: 0px 2px 5px 0px #ccc;
    font-family: 'Nunito sans', 'san-serif';
  }

  
  
  
  @media screen and (max-width: 768px){
  
    .search__area {
      margin-top:  30px;
      align-items: flex-start !important;
      flex-direction: column;
      
    }
  
    .search__box1 {
      width: 100%;
      margin-bottom: 30px;
      
    }
  
    .search__box2 .input__select{
      width: 210px !important;
    }

  }
.details__btn{
    margin: 50px 0 !important;
}

.back__link{
    text-decoration: none !important;
}

.right__col img{
    width: 200px;
    height: auto;
    object-fit: contain;
}

.left__col{
    padding: 20px 30px  !important;
}

.left__col h2{
    font-size: 32px;
    color: #333;
    margin: 20px 0;
}
.country__info{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.country__info__left p, .country__info__right p{
    margin: 10px 0;
}

.country__info__left p label, .country__info__right p label{
    font-size: 14.5px;
    color: #444;
    font-weight: bold;
}

.border__countries{
    margin: 50px 0;
}

.border__link{
    color: #444;
    text-decoration: none;
    transition: .5s all;
}

.border__link:hover .border{
    background: #fff;
}

.border__countries label{
    font-size: 14.5px;
    color: #444;
    font-weight: bold;
    margin-right: 10px;
}

.border__countries p{
    display: flex;
    flex-wrap: wrap;
    line-height: 2.5;
}

.border__countries p .border{
    box-shadow: 0 0 0.3em #ccc;
    padding: 5px 20px;
    margin: 5px 5px;
    font-size: small;
}

.country__flag{
    object-fit: cover;
}

@media screen and (max-width: 1068px){


    .border__countries p label{
        display: block;
        margin-bottom: 15px;
    }

}

@media screen and (max-width: 768px){

    .left__col{
        padding: 11px !important;
    }

    

    .country__flag{
        height: 30vh !important;
    }
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background: hsl(0, 0%, 98%);
  font-family: 'Nunito Sans', sans-serif !important;
}


